@import url(https://fonts.googleapis.com/css2?family=Barlow&display=swap);
body {
  margin: 0;
  font-family: 'Barlow', sans-serif;
  font-size: 20px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  align-items: center;
  background-color: #282c34;
  color: white;
}

.home-text-container {
  text-align: center;
  width: 60%;
}

button {
  margin-top: 5%;
  margin-bottom: 5%;
  padding: 15px 32px;
  border-radius: 100px;
  border: none;
  background-color: #9CFFFA;
  font-family: 'Barlow', 'sans-serif';
  font-size: 20px;
  font-weight: bold;
  transition-duration: 0.4s;
}

button:hover {
  background-color: #75B8C8;
}

.success-container {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  align-items: center;
  justify-content: center;
  background-color: #282c34;
  color: white;
}

.success-text-container {
  text-align: center;
  width: 60%;
}

button {
  margin-top: 5%;
  margin-bottom: 5%;
  padding: 15px 32px;
  border-radius: 100px;
  border: none;
  background-color: #9CFFFA;
  font-family: 'Barlow', 'sans-serif';
  font-size: 20px;
  font-weight: bold;
  transition-duration: 0.4s;
}

button:hover {
  background-color: #75B8C8;
}

.error-container {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  align-items: center;
  justify-content: center;
  background-color: #282c34;
  color: white;
}

.error-text-container {
  text-align: center;
  width: 60%;
}

.api-container {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  align-items: center;
  background-color: #282c34;
  color: white;
}

.api-button {
  margin-top: 2%;
  margin-bottom: 5%;
  margin-right: 2%;
  padding: 15px 32px;
  border-radius: 100px;
  border: none;
  background-color: #9CFFFA;
  font-family: 'Barlow', 'sans-serif';
  font-size: 20px;
  font-weight: bold;
  transition-duration: 0.4s;
}

.api-button:hover {
  background-color: #75B8C8;
}

.btn-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}

input {
  margin-top: 1%;
  margin-bottom: 2%;
  border: 1px solid #ccc;
  border-radius: 20px;
  padding: 12px 20px 12px 20px;
}

